// Custom style: Only effect under new breakpoints
@include media-breakpoint-up(xxl) {
   // Sample style
}

@include media-breakpoint-up(xxxl) {
   // Sample style
}

@each $key, $value in $border-widths {
  .border-#{$key} { border-width: $value !important; }
}

.rounded-xl { border-radius: $border-radius-xl !important; }
.rounded-xxl { border-radius: $border-radius-xxl !important; }

